import React,{useEffect, useRef, useState} from "react";
import { GadgetsSpecification, Box } from "./gadget/style/gadgetStyle";
//import { toJS } from "mobx";
import { DhProperties, getDefaultImage } from "../DhProperties";
import { MobileModelItemContainer } from "./gadget/style/gadgetStyle";
import styled from "styled-components";

const MobileCard = styled(Box)`
  tr{
    td.leftImage{
      .imgHolder{
        min-width:53px;width:53px;max-width:53px;min-height:115px;height:115px;max-height:115px;
      }
    }
  } 
`;


export default function MobileModelItem({ data, isFallbackCase, lang }) {

  //const imgRef = useRef();
  const [imageLoadError, setImageLoadError] = useState(false);

  console.log("MobileModelItem: isFallbackCase: ", isFallbackCase);

  const onItemClick = (id, product, title = '',) => {
    try {
      if (typeof (newsHuntAction) != 'undefined' && typeof (callEvent) != "undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS))) && id && product) {
        const url = DhProperties.properties.pwaBaseUrl + "/webItem?webModel=" + encodeURIComponent(
          JSON.stringify({
            title: title,
            url: `${data.baseUrl ? data.baseUrl : DhProperties.properties.baseUrl}/details?product=${product}&id=${id}&lang=${lang ?? "en"}`
          })
        );
        callEvent(url);
      } else if (product && id) {
        window.location.assign(`${data.baseUrl ? data.baseUrl : DhProperties.properties.baseUrl}/details?product=${product}&id=${id}&lang=${lang ?? "en"}`);
      }
    } catch (err) {
      console.log("MobileModelItem: ", err);
    }
  }


  return (
    <MobileModelItemContainer isFallbackCase={isFallbackCase}>
      <MobileCard onClick={()=>onItemClick(data.id, data.product, data.name)} key={data.id}>
        <tr>
          <td rowSpan="2" className="leftImage">
            <div className="imgHolder"> 
            {data.image && <img src={imageLoadError?getDefaultImage(data.product):data.image} alt={data.model} onError={()=>setImageLoadError(true)}/>}
            {/* { data.image && <LazyImage src={data.image} alt={data.alt} thumb={data.thumb} imgRef={imgRef} product={data.product}/>} */}
            </div>
          </td>
          <td className="content">
            <p className="model">{data.name}</p>
          </td>
        </tr>
        <tr>
          <td className="content textBottom">
            <p className="price">
              From <span>â‚¹ {data.price}</span>
            </p>
          </td>
        </tr>
      </MobileCard>
      <GadgetsSpecification>{data.info}</GadgetsSpecification>
    </MobileModelItemContainer>
  );
}





